export const LIBRARY_NAME = "winlet";

export type WindowState = "normal" | "minimized" | "maximized";

export interface WindowContentOptions {
	/**
	 * HTML
	 */
	html?: string;
	/**
	 * iframe
	 */
	iframe?: {
		src?: HTMLIFrameElement["src"];
		srcdoc?: HTMLIFrameElement["srcdoc"];
		allow?: HTMLIFrameElement["allow"];
		referrerPolicy?: HTMLIFrameElement["referrerPolicy"];
		loading?: HTMLIFrameElement["loading"];
		sandbox?: string[] | string;
	};
	/**
	 * HTMLselector
	 */
	template?: string;
}

// IWindow
export interface IWindow {
	readonly id: string;
	options: Required<WindowOptions>;
	el: HTMLElement;
	state: WindowState;

	// --- Public API ---
	close(): void;
	minimize(): void;
	toggleMaximize(): void;
	maximize(): void;
	restore(): void;
	focus(): void;
	blur(): void;
	getTitle(): string;
	setTitle(title: string): void;
	setIcon(icon: string | null): void;
	getPosition(): { x: number; y: number };
	setPosition(x: number | "center" | "left" | "right", y: number | "center" | "top" | "bottom"): void;
	getSize(): { width: number; height: number };
	setSize(width: number | string, height: number | string): void;
	reload(): void;
}

export interface MenuItem {
	name?: string;
	action?: (win: IWindow) => void;
	items?: MenuItem[];
	separator?: boolean;
	shortcut?: string;
}

export interface TabItem {
	title: string;
	content: WindowContentOptions;
}

export interface ContextMenuItem {
	name?: string;
	action?: (win: IWindow) => void;
	separator?: boolean;
}

export interface WindowOptions {
	/**
	 * ID
	 */
	id?: string | null;
	/**
	 * 
	 *
	 * focus (): 
	 * prevent: 
	 * create: ID
	 */
	ifExists?: "focus" | "prevent" | "create";
	/**
	 * 
	 */
	title?: string;
	/**
	 * 
	 *
	 * url / font-awesome
	 */
	icon?: string | null;
	/**
	 * X
	 */
	x?: number | "center" | "left" | "right";
	/**
	 * Y
	 */
	y?: number | "center" | "top" | "bottom";
	/**
	 * X
	 */
	width?: number;
	/**
	 * Y
	 */
	height?: number;
	/**
	 * X
	 */
	minWidth?: number;
	/**
	 * Y
	 */
	minHeight?: number;
	/**
	 * 
	 */
	resizableX?: boolean;
	/**
	 * 
	 */
	resizableY?: boolean;
	/**
	 * 
	 */
	movable?: boolean;
	/**
	 * 
	 */
	closable?: boolean;
	/**
	 * 
	 */
	minimizable?: boolean;
	/**
	 * 
	 */
	maximizable?: boolean;
	/**
	 * 
	 */
	maximizableOnDblClick?: boolean;
	/**
	 * 
	 */
	enableShortcuts?: boolean;
	/**
	 * 
	 */
	controlsPosition?: "left" | "right";
	/**
	 * 
	 */
	content?: WindowContentOptions;
	/**
	 * 
	 */
	menu?: MenuItem[];
	/**
	 * 
	 */
	tabs?: TabItem[];
	tabOptions?: {
		/**
		 * 
		 */
		reorderable?: boolean;
		/**
		 * 
		 */
		closable?: boolean;
		/**
		 * 
		 */
		addable?: boolean;
		/**
		 * 
		 */
		onAdd?: (win: IWindow) => TabItem;
	};
	/**
	 * 
	 */
	contextMenu?: ContextMenuItem[];
	/**
	 * 
	 */
	onOpen?: (win: IWindow) => void;
	/**
	 * 
	 */
	onClose?: (win: IWindow) => void;
	/**
	 * 
	 */
	onFocus?: (win: IWindow) => void;
	/**
	 * 
	 */
	onBlur?: (win: IWindow) => void;
	/**
	 * 
	 */
	onResize?: (win: IWindow) => void;
	/**
	 * 
	 */
	onMove?: (win: IWindow) => void;
	/**
	 * 
	 *
	 * iframehtml `false` 
	 */
	onReload?: (win: IWindow) => boolean | void;
}

export interface GlobalConfigOptions {
	windowSwitchShortcut?: string | null; // : "Ctrl+`"
}

// WinLetAPI
export interface WinLetApi {
	init: () => void;
	createWindow: (options?: WindowOptions) => IWindow;
	getWindow: (id: string) => IWindow | undefined;
	getActiveWindow: () => IWindow | null;
	setDefaultConfig: (options: WindowOptions) => void;
	setGlobalConfig: (options: GlobalConfigOptions) => void;
	get version(): string;
}

// WindowWinLet
declare global {
	interface Window {
		WinLet: WinLetApi;
	}
}
